body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  margin: 0;
  padding: 0;
}

.wrapper {
  display: flex;
  flex-direction: column;
}

.first-half {
  height: 100vh;
}

.second-half {
  margin: auto 20px;
  border-left: black;
  border-left-style: solid;
  border-left-width: thin;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.product-canvas {
  height: 100vh;
}

.configurator {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 50px auto;
  width: 60%;
}

.configurator .select-type {
  padding: 8px;
  display: flex;
  flex-direction: row;
  text-align: center;
}

.select-color {
  position: relative;
  display: flex;
  /* width: 150px; */
  justify-content: space-between;
  padding: 10px 0;
}

.select-color > a {
  padding: 5px;
  /* border: 0px solid black; */
  margin: 5px;
  min-width: 50px;
  border-radius: 3px;
  box-shadow: 3px 3px 2px rgba(0, 0, 0, 0.3);
  cursor: pointer;
}

.switch {
  position: relative;
  display: inline-block;
  width: 52px;
  height: 26px;
  margin-left: 10px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 34px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 50%;
}

input:checked + .slider {
  background-color: #2196f3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}
.container {
  display: flex;
  padding: 0 30px;
}

.content {
  flex: 2.5;
}

.controls {
  flex: 1.5;
  margin-left: 20px; /* Add margin for spacing between content and controls */
}

.type-heading {
  display: flex;
  align-items: center;
  width: 200px;
}

input {
  display: block;
  box-sizing: border-box;
  width: 90px;
  /* margin: 20px 55px 0; */
  padding: 6px;
  border: 1px solid #ddd;
  border-radius: 4px;
  background: #eee;
  outline: none;
  font: inherit;
  text-transform: uppercase;
  text-align: center;
}

input:focus {
  border-color: #4298ef;
}

.react-colorful {
  position: absolute;
  top: 50px;
  left: 0;
  z-index: 9;
}
